<template>
  <nav class="nav">
    <!-- 訊息顯示 -->
    <transition name="bounce">
      <v-snackbar
        top
        :color="this.$store.state.snackbarTheme"
        :value="this.$store.state.snackbar"
        @input="close"
      >
        <span>{{ this.$store.state.snackbarMsg }}</span>
        <v-btn text @click.native="close(false)">Close</v-btn>
      </v-snackbar>
    </transition>

    <v-app-bar :color="$store.state.colorSet.bgColor3" app fixed>
      <v-toolbar-title>
        <router-link to="/main/functionlist" tag="div" style="cursor: pointer">
          <span class="font-weight-black white--text">
            {{ this.$store.state.cData.company_name }}
          </span>
          <span style="font-size: 12px; color: white">
            {{ this.$store.state.verMsg }}
          </span>
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn text color="white" @click.stop="drawer = !drawer">
        目前使用者:{{ this.$store.state.pData.username }}
        <v-icon>mdi-apps</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer app temporary :color="$store.state.colorSet.bgColor3" v-model="drawer" right>
      <v-list-item-title class="text-center white--text my-2"
        >功能選擇</v-list-item-title
      >
      <v-divider color="white"></v-divider>

      <v-list dense rounded nav>
        <v-list-item v-for="item in items" :key="item.label">
          <router-link
            tag="div" style="cursor: pointer"
            :to="item.route"
            class="d-flex"
            v-if="item.keyName != 'exit'"
          >
            <v-list-item-icon>
              <v-icon class="yellow--text">{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title class="body-1 yellow--text">{{
                item.label
              }}</v-list-item-title>
            </v-list-item-content>
          </router-link>

          <router-link
            :to="item.route"
            class="d-flex"
            v-if="item.keyName == 'exit'"
          >
            <v-list-item-icon @click="logout">
              <v-icon class="yellow--text">{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content @click="logout">
              <v-list-item-title class="body-1 yellow--text">{{
                item.label
              }}</v-list-item-title>
            </v-list-item-content>
          </router-link>
        </v-list-item>
      </v-list>
      <!-- <pre>
      {{ items }}
      </pre> -->
    </v-navigation-drawer>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      drawer: false,
    };
  },
  computed: {
    items() {
      //2021.8.26 加入藥師權限pharmacist_key -> 只能看到登出的選項
      if (this.$store.state.pData.pharmacist_key) {
        return [
          {
            label: "登出",
            class: "ma-1 white--text",
            color: "dark",
            keyName: "exit",
            image: "/img/text_exit.0db2f3a5.png",
            route: "/login",
            authKey: "exit_key",
            icon: "mdi-logout",
          },
        ];
      } else {
        return this.$store.state.authKeys.filter(
          (item) =>
            this.$store.state.pData[item.authKey] == "true" ||
            item.authKey == "pass" ||
            item.authKey == "exit_key"
        );
      }
    },
  },
  mounted() {
    this.getSessionStorage(); //取得暫存的廠商及使用者資料
    // this.checkAuth(); //判斷授權的功能
  },
  methods: {
    close(val) {
      var pop = { msg: "", type: val };
      this.$store.commit("snackbar", pop);
    },
    logout() {
      this.$store.commit("logined", false);
      this.$router.push("/login");
      sessionStorage.clear(); //清除sessionStorage
    },
    getSessionStorage() {
      if (sessionStorage.getItem("pData") != null) {
        this.$store.commit(
          "setcData",
          JSON.parse(sessionStorage.getItem("cData"))
        );
        this.$store.commit(
          "setpData",
          JSON.parse(sessionStorage.getItem("pData"))
        );
      } else {
        this.$router.push("/login");
      }
    },
    // checkAuth(authkey) {
    //   if (
    //     this.$store.state.pData[authkey] == "true" ||
    //     authkey == "pass" ||
    //     authkey == "exit_key"
    //   ) {
    //     return true;
    //   }
    // },
  },
};
</script>

<style lang="scss">
.v-app-bar {
  background-image: url("../assets/BG_top.png");
  background-repeat: repeat;
}

.v-snack__content {
  font-size: 2rem;
}
</style>